
import axios from 'axios'

import Vue from 'vue';
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

Vue.component(Popup.name, Popup);
export default {
data(){

return {
contents:{rights:'评分说明',title:'遵纪守法评分'},
list:[
{name:"侯宇茜",score:"20",tempint:3},
{name:"申竣华",score:"21",tempint:3},
{name:"海依沄",score:"20",tempint:3},
{name:"卞漫诗",score:"18",tempint:3}
],
isYellow:false,
showPop:false
}
}, filters: {
Upper: function (value) {
try {
if(value===3) throw "去处理";
if(value=== 1)  throw "已审核";
if(value=== 0)  throw "待审核";
}
catch(err) {
return value=err;
}
},
Upper1: function (value) {
try {
if(value===null) throw "yellowA";
if(value=== 1)  throw "yellowB";
if(value=== 0)  throw "yellowC";
}
catch(err) {
return value=err;
}

}
},
components:{
XHeader,
ViewBox,
Popup,
Flexbox,
FlexboxItem
},
directives:{
TransferDom
},
methods:{
changeItem(item){
this.$router.push({
path: 'lawAbidingDetail',
params: {
partmentId:item.id,
name:encodeURI(item.name),
departmentId:item.departmentid,
userId:item.userid
}
})
},
showMenu(){
this.showPop = true;
},
know(){
this.showPop = false
},
}



}



